<script>
import { mixins } from "../../mixins/index";
import Video from "video.js";
import "video.js/dist/video-js.css";
import test from "../../static/video/test.mp4";
import test1 from "../../static/video/test1.mp4";
import testImg from "../../static/video/test.webp";
import test1Img from "../../static/video/test1.webp";

import { Toast } from "vant";
export default {
  mixins: [mixins],
  data() {
    return {
      active: "0",
      attentionList: [],
      videoList: [],
      myPlayerList: [],
      list: [
        {
          id: 1,
          name: "苟富贵1",
          address: "呼和浩特市",
          text: "saijd但是第三季度还剩多少搭搭撒撒发刚发个润体乳人梵蒂冈电饭锅电饭锅代发给代发给代发过大车热特特",
          distance: 1.8,
          age: 23,
          total: 7880,
          isAtt: true,
          images: [
            { url: "1" },
            { url: "2" },
            { url: "3" },
            { url: "4" },
            { url: "5" },
            { url: "6" },
          ],
        },
        {
          id: 2,
          name: "苟富贵2",
          address: "呼和浩特市",
          text: "saijd但是第三季度还剩多少搭搭撒撒发刚发个润体乳人梵蒂冈电饭锅电饭锅代发给代发给代发过大车热特特",
          distance: 1.8,
          age: 23,
          total: 7880,
          isAtt: false,
          // "https://baikevideo.cdn.bcebos.com/media/mda-Ofj5DOML8EqV0Umy/da4f175769bcf693d0d0e205e1546015.mp4"
          video: test,
          posterImg: testImg,
        },
        {
          id: 3,
          name: "苟富贵3",
          address: "呼和浩特市",
          text: "saijd但是第三季度还剩多少搭搭撒撒发刚发个润体乳人梵蒂冈电饭锅电饭锅代发给代发给代发过大车热特特",
          distance: 1.8,
          age: 23,
          total: 7880,
          isAtt: false,
          images: [
            { url: "1" },
            { url: "2" },
            { url: "3" },
            { url: "4" },
            { url: "5" },
            { url: "6" },
            { url: "7" },
            { url: "8" },
          ],
        },
        {
          id: 4,
          // https://baikevideo.cdn.bcebos.com/media/mda-XM4SdPNjvL51hpe3/125beaaee7d73600de68bdfc8d8cfc8a.mp4id: 2,
          name: "肉夹馍",
          address: "西安市",
          text: "腊汁肉以猪肉为原料，其历史可追溯到战国时代，那时称“寒肉”，唐代又称“腊肉”，战国时期当时位于秦晋豫三角地带的韩国，已能制作腊汁肉了，秦灭韩后，制作工艺传进长安，因此现在的肉夹馍也叫腊汁肉夹馍",
          distance: 1.8,
          age: 23,
          total: 7880,
          isAtt: false,
          video: test1,
          posterImg: test1Img,
        },
        {
          id: 5,
          name: "肉肉",
          address: "上海市",
          text: "我爱吃腊汁肉夹馍",
          distance: 1.2,
          age: 24,
          total: 1879,
          isAtt: false,
        },
      ],
    };
  },
  mounted() {
    this.list.map((item) => {
      if (item.isAtt) {
        this.attentionList.push(item);
      }
      if (item.video) {
        this.videoList.push(item);
      }
    });
    this.videoList.map((item) => {
      this.myPlayerList.push(
        Video(
          `myVideo${item.id}`,
          {
            controls: true, //显示底部控件
            muted: true, // 静音播放
            autoplay: false, // 如果true,浏览器准备好时开始回放。
            playsinline: true,
            preload: "auto", // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据
          },
          function () {
            // 事件
          }
        )
      );
    });
  },
  beforeDestroy() {
    if (this.myPlayerList) {
      this.myPlayerList.map((item) => {
        item.dispose();
      });
    }
  },
  methods: {
    toDetail(item) {
      this.$router.push({ name: "dynamicDetails", params: item });
    },
    toUser(item) {
      this.$router.push({ name: "user", params: item });
    },
    attention(item, index) {
      this.list[index].isAtt = !this.list[index].isAtt;
      if (item.isAtt) {
        Toast.success("已关注");
        this.attentionList.push(this.list[index]);
      } else {
        Toast.success("取消关注成功");
        this.attentionList.map((itemL, indexL) => {
          if (itemL.id == item.id) {
            this.attentionList.splice(indexL, 1);
          }
        });
      }
    },
    changeList(item, index) {
      this.attentionList.splice(index, 1);
      this.list.map((itemL, indexL) => {
        if (itemL.id == item.id) {
          this.list[indexL].isAtt = false; // 取消关注
        }
      });
    },
  },
};
</script>

